.aboutPage{
    height: 380px;
    background-color: grey;
}
span{
    font-size: 28px;
}
.avater{
    width: 300px;
    border-radius: 50%;
    margin-top: 100px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    margin-bottom: 30px;
}
.hero{
    height: 100%;
    width: 100%;
}
.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 68%;
    left: 50%;
    text-align: center;
    color: black;
}
.content p{
    font-size: 20px;
    font-weight: 500;
}
@media screen and (max-width:885px){
    .aboutPage{
        height: 480px;
        background-color: grey;
    }
    .hero .content{
        position: absolute;
        transform: translate(-50%, -50%);
        top: 112%;
        left: 50%;
        text-align: center;
        color: black;
    }
   
}
@media screen and (max-width:500px){
    .hero .content{
        position: absolute;
        transform: translate(-50%, -50%);
        top: 108%;
        left: 50%;
        text-align: center;
        color: black;
    }
    .aboutPage{
        height: 480px;
        background-color: grey;
    }
   
}